import { Component, OnInit } from '@angular/core';
import {ServerMatch} from "../shared/servermatch";
import {Router} from "@angular/router";

@Component({
  selector: 'app-tools-page',
  templateUrl: './tools-page.component.html',
  styleUrls: ['./tools-page.component.css']
})
export class ToolsPageComponent implements OnInit {

  elements: any;

  constructor(private router: Router) {
    let TIU = ServerMatch.STATIC + 'assets/images/print-logo.png';
    this.elements = {
      home: {
        title: 'Home',
        imageUrl: TIU,
        routeTo: 'homize',
      },
      heat: {
        title: 'Heat',
        imageUrl: TIU,
        routeTo: 'heat',
      },
      move: {
        title: 'Move',
        imageUrl: TIU,
        routeTo: 'move',
      },
      ext: {
        title: 'Extrude',
        imageUrl: TIU,
        routeTo: 'extrude',
      },
      return: {
        title: 'بازگشت',
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
        routeTo: 'home',
      },
      bed: {
        title: 'Bed Leveling',
        imageUrl: TIU,
        routeTo: 'bedleveling',
      },
      fan: {
        title: 'Fans',
        imageUrl: TIU,
        routeTo: 'setfans',
      }
    };
  }

  ngOnInit() {
  }

  goToPage(route) {
    this.router.navigate(['/' + route]);
  }

}



