import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';


import {AppComponent} from './app.component';
import {MainPageComponent} from './main-page/main-page.component';
import {SelectUsbComponent} from './select-usb/select-usb.component';
import {routing} from "./app.routing";
import {FlexLayoutModule} from "@angular/flex-layout";
import {
  MatCardModule, MatDialogModule,
  MatIconModule,
  MatProgressBarModule,
  MatProgressSpinnerModule,
  MatSliderModule,
  MatSnackBarModule
} from "@angular/material";
import {HttpService} from "./services/http.service";
import {HttpClientModule} from "@angular/common/http";
import {ProgressService} from "./services/progress.service";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {ToolsPageComponent} from './tools-page/tools-page.component';
import {HomizeComponent} from './tools-page/homize/homize.component';
import {MoveAxisComponent} from './tools-page/move-axis/move-axis.component';
import {PrintPageComponent} from './print-page/print-page.component';
import {DataService} from './services/data.service';
import { ConfirmComponent } from './shared/confirm/confirm.component';


@NgModule({
  declarations: [
    AppComponent,
    MainPageComponent,
    SelectUsbComponent,
    ToolsPageComponent,
    HomizeComponent,
    MoveAxisComponent,
    PrintPageComponent,
    ConfirmComponent,
  ],
  imports: [
    routing,
    BrowserAnimationsModule,
    BrowserModule,
    HttpClientModule,
    FlexLayoutModule,
    MatSnackBarModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatIconModule,
    MatSliderModule,
    MatCardModule,
    MatDialogModule,
  ],
  entryComponents: [
    ConfirmComponent,
  ],
  providers: [HttpService, ProgressService, DataService],
  bootstrap: [AppComponent]
})
export class AppModule {
}



