import {RouterModule, Routes} from '@angular/router';
import {MainPageComponent} from "./main-page/main-page.component";
import {AppComponent} from "./app.component";
import {SelectUsbComponent} from "./select-usb/select-usb.component";
import {ToolsPageComponent} from "./tools-page/tools-page.component";
import {HomizeComponent} from "./tools-page/homize/homize.component";
import {MoveAxisComponent} from "./tools-page/move-axis/move-axis.component";
import {PrintPageComponent} from './print-page/print-page.component';

const APP_ROUTES: Routes = [
  {path: '', redirectTo: 'home', pathMatch: 'full'},
  {path: '', component: AppComponent},
  {path: 'home', component: MainPageComponent},
  {path: 'select', component: SelectUsbComponent},
  {path: 'tools', component: ToolsPageComponent},
  {path: 'homize', component: HomizeComponent},
  {path: 'move', component: MoveAxisComponent},
  {path: 'print', component: PrintPageComponent},
];

export const routing = RouterModule.forRoot(APP_ROUTES);



