import {Component, OnInit} from '@angular/core';
import {HttpService} from "../../services/http.service";
import {Router} from "@angular/router";
import {ProgressService} from "../../services/progress.service";
import {ServerMatch} from "../../shared/servermatch";

interface Axis {
  X: Number;
  Y: Number;
  Z: Number;
}

@Component({
  selector: 'app-move-axis',
  templateUrl: './move-axis.component.html',
  styleUrls: ['./move-axis.component.css']
})
export class MoveAxisComponent implements OnInit {

  elements: any;
  value: number;
  axis: Axis;

  constructor(private httpService: HttpService, private router: Router,
              private progressService: ProgressService) {
    this.elements = {
      up: {
        imageUrl: ServerMatch.STATIC + 'assets/images/up.png'
      },
      down: {
        imageUrl: ServerMatch.STATIC + 'assets/images/down.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      }
    }
  }

  ngOnInit() {
    this.axis = {
      X: 0,
      Y: 0,
      Z: 0,
    };
  }

  moveAxis(axis, value) {
    // if(this.axis[axis] + value < 0) {
    //   console.log("YOU'RE SETTING IT TO MINUS!");
    //   return;
    // }

    this.progressService.enable();
    this.httpService.post('move_axis', {axis, value}).subscribe(
      data => {
        this.progressService.disable();
        this.axis[axis] += value || 0;
      },
      err => {
        this.progressService.disable();
        console.error("couldn't move axis", err);

        // TODO: for test purposes
        // this.axis[axis] += value || 0;
      }
    );
  }

  goToToolsPage() {
    this.router.navigate(['/tools']);
  }

  changeSlider($event) {
    this.value = $event.value;
    console.log(this.value);
  }

  precisionRound(number, precision) {
    var factor = Math.pow(10, precision);
    return Math.round(number * factor) / factor;
  }

}



